.mw-ref {
  vertical-align: super;
  line-height: 1;
  font-size: smaller;
  font-weight: normal;
  font-style: normal;
  unicode-bidi: -moz-isolate;
  unicode-bidi: -webkit-isolate;
  unicode-bidi: isolate
}

.mw-ref-follow {
  display: none
}

.mw-ref a[data-mw-group]:after {
  content: '[' attr(data-mw-group) ' ' counter(mw-Ref,decimal) ']'
}

.mw-ref a[data-mw-group='decimal']:after {
  content: '[' counter(mw-Ref,decimal) ']'
}

.mw-ref a[data-mw-group='lower-alpha']:after {
  content: '[' counter(mw-Ref,lower-alpha) ']'
}

.mw-ref a[data-mw-group='upper-alpha']:after {
  content: '[' counter(mw-Ref,upper-alpha) ']'
}

.mw-ref a[data-mw-group='lower-greek']:after {
  content: '[' counter(mw-Ref,lower-greek) ']'
}

.mw-ref a[data-mw-group='lower-roman']:after {
  content: '[' counter(mw-Ref,lower-roman) ']'
}

.mw-ref a[data-mw-group='upper-roman']:after {
  content: '[' counter(mw-Ref,upper-roman) ']'
}

.mw-ref a[data-mw-group='error-test']:after {
  content: '[' counter(mw-Ref,symbols(cyclic 'first' 'second' 'last!')) ']'
}

.mw-ref a:after {
  content: '[' counter(mw-Ref,decimal) ']'
}

x-thereisnoelementwiththisname:after,span.mw-reflink-text {
  display: none
}

ol.mw-references {
  counter-reset: mw-ref-linkback list-item
}

ol.mw-references > li {
  counter-increment: mw-ref-linkback
}

[rel='mw:referencedBy'] {
  counter-reset: mw-ref-linkback -1
}

[rel='mw:referencedBy']:before {
  content: ' '
}

span[rel='mw:referencedBy'] a:before {
  counter-increment: mw-ref-linkback;
  content: counters(mw-ref-linkback,'.',decimal);
  vertical-align: super;
  line-height: 1;
  font-size: smaller
}

span[rel='mw:referencedBy'] a:after {
  content: ' '
}

x-thereisnoelementwiththisname[rel='mw:referencedBy']:before,span.mw-linkback-text {
  display: none
}

.mw-cite-backlink,.cite-accessibility-label {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none
}

.mw-references-columns {
  -webkit-column-width: 30em;
  -moz-column-width: 30em;
  column-width: 30em;
  margin-top: 0.3em
}

.mw-parser-output .mw-references-columns .references {
  margin-top: 0
}

.mw-references-columns li {
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid;
  break-inside: avoid-column
}

ol.references {
  counter-reset: mw-ref-extends-parent list-item
}

ol.references > li {
  counter-increment: mw-ref-extends-parent;
  counter-reset: mw-ref-extends-child
}

ol.references .mw-extended-references {
  list-style: none;
  margin-left: 2em
}

.mw-extended-references > li:before {
  counter-increment: mw-ref-extends-child;
  content: counter(mw-ref-extends-parent,decimal) '.' counter(mw-ref-extends-child,decimal) '. '
}

sup.reference {
  unicode-bidi: -moz-isolate;
  unicode-bidi: -webkit-isolate;
  unicode-bidi: isolate;
  white-space: nowrap;
  font-weight: normal;
  font-style: normal
}

ol.references li:target,sup.reference:target {
  background-color: #eaf3ff
}

.mw-ext-cite-error {
  font-weight: bold;
  unicode-bidi: embed
}

.mw-ext-cite-error code {
  background-color: transparent;
  color: inherit;
  padding: 1px 2px
}

.mw-cite-dir-ltr .reference-text {
  direction: ltr;
  unicode-bidi: embed
}

.mw-cite-dir-rtl .reference-text {
  direction: rtl;
  unicode-bidi: embed
}

@media print {
